function setVolume(content) {
    $_vikon('#is-volume-on').on('click', () => {
        if (sessionStorage.getItem('is-volume-on') === '0') {
            $_vikon('#special-volume-logo').addClass('fa-volume');
            $_vikon('#special-volume-logo').removeClass('fa-volume-off');
            sessionStorage.setItem('is-volume-on', '1');
            content.addClass('special-volume-on');
            content.on('mouseup', handleVolume);
        } else {
            $_vikon('#special-volume-logo').addClass('fa-volume-off');
            $_vikon('#special-volume-logo').removeClass('fa-volume');
            content.removeClass('special-volume-on');
            sessionStorage.setItem('is-volume-on', '0');
            content.off('mouseup', handleVolume);
            $_vikon('body #volume_start').remove();
        }
        checkAndRebuildBlocks();
    });
}

