function getSelection() {
    if (window.getSelection) {
        return window.getSelection();
    }
    if (document.getSelection) {
        return document.getSelection();
    }
    return null;
}

export async function handleVolume(e) {
    if ((!!e.target.id && e.target.id === 'volume_start') || mutex === true) {
        return;
    }
    $_vikon('body #volume_start').remove();
    let text = null;
    let yCord = null;
    const selection = getSelection();
    if (selection !== null && selection.rangeCount > 0) {
        const rect = selection.getRangeAt(0).getBoundingClientRect();
        text = selection.toString();
        yCord = rect.y + $_vikon('html').scrollTop();
    }

    if (!!text && !!yCord) {
        const context = new AudioContext();
        const el = $_vikon(`${'<a class="voice_btn btn btn-lg no-margin" id="volume_start" href="#" style="'
        + 'z-index:10000; position: absolute; top: '}${Math.ceil(yCord)}px; right: 15px;">Озвучить</a>`);

        if (text.length < 1300) {
            el.one('click', async (event) => {
                mutex = true;
                const vikonDomain = $_vikon('[name=vikon_domain]').val();
                try {
                    event.preventDefault();
                    el.text('Обрабатывается...')
                        .attr('style', (i, s) => {
                            return `${s || ''} text-decoration: none !important;`;
                        });

                    const response = await fetch(`${vikonDomain}/api/open/sound/generate`, {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json',
                        },
                        body: JSON.stringify({
                            text,
                            token: window.sessionStorage ? sessionStorage.getItem('token') : null,
                        }),
                    });

                    if (response.ok) {
                        const data = await response.blob();

                        const buffer = await context.decodeAudioData(await data.arrayBuffer());
                        const source = context.createBufferSource();
                        source.buffer = buffer;
                        source.connect(context.destination);
                        source.start(0);
                        el.text('Воспроизводится...');
                        source.addEventListener('ended', () => {
                            el.remove();
                            mutex = false;
                        }, {once: true});
                    } else {
                        console.log(await response.json());
                        el.text('Не удалось озвучить выделенный текст');
                    }
                } catch (ex) {
                    mutex = false;
                    console.log(ex);
                    el.text('Не удалось озвучить выделенный текст');
                }
            });
        } else {
            el.text('Невозможно озвучить - слишком длинный выделенный текст');
        }
        $_vikon('body').append(el);
    }
}
